<template>
  <div class="text-xs" v-if="this.time">
    <vue-countdown :time="this.time" v-slot="{ days, hours, minutes, seconds }">
      Time Remaining：{{ days }} days, {{ hours }} hours, {{ minutes }} minutes,
      {{ seconds }} seconds.
    </vue-countdown>
  </div>
</template>
<script>
export default {
  props: ["start"],
  computed: {
    startDate() {
      return new Date(this.start);
    },
    time() {
      var now = new Date();
      var timePassedMs = now.getTime() - this.startDate.getTime();
      var fourDaysMs = 86400000 * 4;
      var r = fourDaysMs - timePassedMs;
      if (r < 0) return 0;
      return r;
    },
  },
};
</script>
